<template>
	<div class="index">
		<div class="any-background">
			<div class="formsome">

				<p class="title"> 欢迎登陆美丽共享联盟 </p>
				<div class="admins">
					账号:
					<el-input class='admi' placeholder="请输入账号" v-model="input" clearable>
					</el-input>
				</div>
				<div class="passwords">
					密码:
					<el-input class='pass'  @keyup.enter.native="logins" placeholder="请输入密码" v-model="inputs" show-password></el-input>
				</div>




				<el-button class='goloding' @click='logins' type="primary">登录</el-button>
				<div class="moider">
					<div></div>
					<!-- <div> <router-link style="color: #fff;" to="/expassword">修改密码</router-link> </div> -->
					<div class="nopass">
						<router-link style="color: #fff;" to="/nopassword/nopassword">忘记密码？</router-link>
					</div>
				</div>
			</div>
		</div>
	</div>
</template>

<script>
	export default {
		name: "index",
		data() {
			return {
				input: '',
				inputs: ''
			};
		},
		methods: {
			logins() {
				this.utils.post({
				    url: 'user',
					data:{
						admin_user:this.input,
						admin_pwd:this.inputs
					}
				  }).then (res=>{
					  window.sessionStorage.setItem( 'dlmessage' , JSON.stringify(res) )
					  if(res.admin_judge == 1){
							this.$store.commit("exchadl", res.admin_judge)
							return
					  }
					 if(res.admin_judge == 2){
					 		this.$store.commit("exchadl", res.admin_judge)
					 		return
					 }
					  
				  })
			}
		},
		components: {},
		mounted() {

		}
	};
</script>

