import axios from 'axios'
import router from '../router'
import { Message,Loading } from 'element-ui'
const ConfigBaseURL = '' //默认路径，这里也可以使用env来判断环境
let loadingInstance = null //这里是loading
//使用create方法创建axios实例

console.log('我执行了 ')


if( window.sessionStorage.getItem("dlmessage")==null ){
	window.sessionStorage.setItem("dlmessage",JSON.stringify( { admin_judge:0 } )  ); 
}
// let admin_judge = JSON.parse(window.sessionStorage.getItem("dlmessage")).admin_judge
	// if (JSON.parse(window.sessionStorage.getItem("dlmessage")).admin_judge == 1 || JSON.parse(window.sessionStorage.getItem("dlmessage")).admin_judge == 2) {
	// 		admin_judge = JSON.parse(window.sessionStorage.getItem("dlmessage")).admin_judge
	// }
const Service = axios.create({
	timeout: 7000, // 请求超时时间
	baseURL: ConfigBaseURL,
	method: 'post',
	headers: {
		'Content-Type': 'application/json;charset=UTF-8'
	},
	data: {
		a:JSON.parse( window.sessionStorage.getItem("dlmessage")) == null,
		admin_judge: JSON.parse( window.sessionStorage.getItem("dlmessage")) == null ? 0 : JSON.parse(window.sessionStorage.getItem("dlmessage")).admin_judge
	}
})
// 添加请求拦截器
Service.interceptors.request.use(config => {
	loadingInstance = Loading.service({
		lock: true,
		text: 'loading...'
	})
	return config
})
// 添加响应拦截器
Service.interceptors.response.use(response => {
	loadingInstance.close()
	if (response.data.code == 0) {
		Message({
			message: '' + response.data.msg,
			type: 'success',
			duration: 2 * 1000
		})
		return response.data.data
	}
	if (response.data.code == 1) {
		
		Message({
			message: '' + response.data.msg,
			type: 'error',
			duration: 3 * 1000
		})
		return 'error'
	} else if (response.data.code == 2) {
		Message({
			message: '' + response.data.msg,
			type: 'error',
			duration: 3 * 1000
		})
		router.push('/')
	}
}, error => {
	const msg = error.Message !== undefined ? error.Message : ''
	Message({
		message: '网络错误' + msg,
		type: 'error',
		duration: 3 * 1000
	})
	loadingInstance.close()
	return Promise.reject(error)
})



export default {
	post:Service
}
