import Vue from 'vue'
import Vuex from 'vuex'
import router from '../router'

Vue.use(Vuex)

export default new Vuex.Store({
	state: {
		username: 1, //是否登录  1否 2 是
		usertocken: '',
		dlsf:0, //1,超级 。 2：普通
		headlist:[]
	},
	mutations: {
		// 修改查看是否登录
		excss(state,ea) {
			state.username = ea
		},
		// 修改查看登录的权限
		exchadl(state,sfbh){
			state.dlsf  =  sfbh
			window.sessionStorage.setItem( 'shuxincishu' , JSON.stringify( false ) )
			router.push('/myider')
			
		},
		// 面包屑的大标题修改
		breadclear(state,ea){
			state.headlist = []
			state.headlist.push({father:1,text:ea})
			
		},
		breadadd(state,ea){
			if(state.headlist.length == 1){
				state.headlist.push({father:2,text:ea})
			}else{
				state.headlist.forEach((item,index)=>{
					if(item.father ==2){
						item.text = ea
					}
				})
			}
			window.sessionStorage.setItem("nbxs",JSON.stringify( state.headlist )  ); 
		}
		
	},
	actions: {},
	modules: {}
})
